<script>
const nativeshare = () => import("nativeshare");
const m_share = () => import("m-share");
var NativeShare, mShare;
import publicTop from "@/components/publicTop.vue";
import qs from "qs";
export default {
  name: "recruitmentinfoForm",
  components: {
    publicTop,
  },
  data() {
    const urlList = {
      qq: "http://connect.qq.com/widget/shareqq/index.html?url={{url}}&title={{title}}&source={{source}}&desc={{desc}}&pics={{image}}&summary={{summary}}",
      weibo:
        "http://service.weibo.com/share/share.php?url={{url}}&title={{title}}&source={{source}}&desc={{desc}}&pic={{image}}&summary={{summary}}",
      qqZone:
        "https://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url={{url}}&title={{title}}&source={{source}}&desc={{desc}}&pics={{image}}&summary={{summary}}",
    };
    return {
      url: require("../assets/bgB.png"),
      zhaopinList: [],
      zhaopinIndex: 9999999,
      decc: "监管所带来",
      isOpen: false,
      tabBanner: [{}, {}, {}, {}],
      contentDetail: {},
      xuanId: "",
      ir: 1,
      tabs: [{ name: "招聘信息" }, { name: "招生简章" }, { name: "联系方式" }],
      isSend: false,
      jianList: [
        {
          selected: false,
          name: "在线简历",
          url: "/a/common/student/resumeinfo?resumeType=0",
        },
        {
          selected: false,
          name: "附件简历",
          url: "/a/common/student/resumeinfo/resumeinfoUpload?resumeType=1",
        },
      ],
      isChoose: false,
      jianData: [],
      isSecond: false,
      jianZhi: [],
      isShare: false,
      shareList: [
        // {
        //   img: require("../assets/wechat.png"),
        //   name: "微信",
        //   nativeshare: "wechatFriend",
        //   m_share: "wx",
        // },
        // {
        //   img: require("../assets/quan.png"),
        //   name: "朋友圈",
        //   nativeshare: "wechatTimeline",
        //   m_share: "wxline",
        // },
        {
          img: require("../assets/qq.png"),
          name: "QQ",
          nativeshare: "qqFriend",
          m_share: "qq",
        },
        {
          img: require("../assets/weibo.png"),
          name: "微博",
          nativeshare: "weibo",
          m_share: "sina",
        },
      ],
      bgc: this.GLOBAL.bgc,
      isOpen4: false,
      isPlay: "block",
      tex: "",
      jianId: "", //选择的简历id
      jianZhiId: "",
      jii: 9,
    };
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  computed: {
    config() {
      return {
        title: "rrrrrrr",
        desc: "描述rrrrr",
        img: require("../assets/quan.png"),
        img_title: "朋友圈",
        link: "当前链接",
      };
    },
  },
  methods: {
    // ppp(){
    //    window.location.href='http://'+this.contentDetail.recruitmentinfo.onlineApplicationUrl
    // },
    toShar(item) {
      if (item.name == "微博") {
        var url =
          "http://v.t.sina.com.cn/share/share.php" +
          "?url=" +
          encodeURIComponent(window.location.href) +
          "&title=" +
          encodeURIComponent("招聘在即，欢迎咨询，万分感谢！") +
          "&source=" +
          encodeURIComponent(
            window.location.href.substring(0, window.location.href.indexOf("?"))
          ) +
          "&summary=" +
          encodeURIComponent(document.title) +
          "&pics=" +
          encodeURIComponent(
            "https://wechat.cdzjkl.com/files/options/avatar1584946687810.jpg"
          ) +
          "&desc=" +
          encodeURIComponent("");
        var a = document.createElement("a");
        a.href = url;
        a.target = "_blank";
        a.click();
      } else if (item.name == "QQ") {
        var url =
          "https://connect.qq.com/widget/shareqq/index.html" +
          "?url=" +
          encodeURIComponent(window.location.href) +
          "&title=" +
          encodeURIComponent("招聘在即，欢迎咨询，万分感谢！") +
          "&source=" +
          encodeURIComponent(
            window.location.href.substring(0, window.location.href.indexOf("?"))
          ) +
          "&summary=" +
          encodeURIComponent(document.title) +
          "&pics=" +
          encodeURIComponent(
            "https://wechat.cdzjkl.com/files/options/avatar1584946687810.jpg"
          ) +
          "&desc=" +
          encodeURIComponent("");
        var a = document.createElement("a");
        a.href = url;
        a.target = "_blank";
        a.click();
      } else {
        console.log(item);
      }
    },
    // 收藏企业
    goingCollect() {
      const params = {
        recruitmentinfo: this.xuanId,
      };
      console.log(params);
      this.$axios
        // .get("/a/collectioninfo/saveCollection?recruitmentinfo=" + this.xuanId)
        .post("/a/collectioninfo/saveCollection", qs.stringify(params))
        .then((res) => {
          console.log(res, "收藏");
          if (res.data.state == 1) {
            this.$toast.success(res.data.msg);
            // this.getDetailContent();
            this.getll();
          } else if (res.data.state == 4) {
            this.$toast.success(res.data.msg);
            // this.getDetailContent();
            this.getll();
          } else if (res.data.state == 2) {
            this.$toast(res.data.msg);
            // 判断去登录
            // window.open(res.data.data);
            window.location.href = res.data.data;
            this.getll();
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    goChD() {
      // this.isOpen = !this.isOpen;
      console.log(this.isOpen4, this.isPlay);
      if (this.isOpen4 == false) {
        this.isOpen4 = true;
      } else {
        this.isOpen4 = false;
